<h1 id="看番二周年" tabindex="-1"><a class="header-anchor" href="#看番二周年" aria-hidden="true">#</a> 看番二周年</h1>
<p>看番二周年，不知不觉过去了这么久，每天一部番，坚持不懈的精神我自己都感动的要哭了，现在已经大三了，没什么余力去看番了（要不是为了重修重修补课想毕业，我还是那个宅男的我），但是我乐于分享。就算无法做到以前那么威武一天一部番，但是一个星期每天看新番打打游戏还是可以的。那么事不宜迟，番名我就放这里了，希望有同好者有道参考。</p>
<p><a href="https://pan.baidu.com/s/1z83qBwo3aE4aProDtMvV1A?pwd=b2wf" target="_blank" rel="noopener noreferrer">日漫动漫合集<ExternalLinkIcon/></a>红色表示强力推荐，紫色表示推荐，蓝色表示一般，黑色表示没看，<a href="https://pan.baidu.com/s/1FzMxxgy8KzLcm6uXg6iCpw?pwd=8888" target="_blank" rel="noopener noreferrer">国漫动漫合集<ExternalLinkIcon/></a> 绿色表示看过，黑色表示没看过，大家可以下载去好好找自己想看的类型，由于之前电脑清理c盘的时候不小心把以前有注释的番删了，所以这里的都没时间去找什么类型，不过大家可以直接到樱花动漫网站自行查找类型看就行了链接也放这里了。<a href="https://odcoc.com/" target="_blank" rel="noopener noreferrer">樱花动漫<ExternalLinkIcon/></a>，给多一个<a href="https://yhdm6.top/" target="_blank" rel="noopener noreferrer">樱花动漫<ExternalLinkIcon/></a>，其实也可以不用在电脑上播放动漫，我这里有手机软件嘘机密<a href="https://pan.baidu.com/s/1uad3jAX0pdShXx-FmdOpKg?pwd=esy8" target="_blank" rel="noopener noreferrer">软件<ExternalLinkIcon/></a></p>
